@charset "UTF-8";/* Icons
diseña: <i class="far fa-object-ungroup"></i>
rellena: <i class="far fa-keyboard"></i>
comparte: <i class="fas fa-share-alt"></i>
arrowup: <i class="fas fa-chevron-up"></i>
phone: <i class="fas fa-mobile-alt"></i>
email: <i class="far fa-envelope"></i>
linkedin: <i class="fab fa-linkedin-in"></i>
github: <i class="fab fa-github-alt"></i>
reset: <i class="far fa-trash-alt"></i>
*/.page_main {
  background: #f5f5f5;
  width: 100%;
  height: auto;
  margin: auto;
}

.wrapper_main {
  margin-right: 35px;
  margin-left: 35px;
  padding-top: 95px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.awesome-image {
  width: 250px;
  height: 63px;
  margin-bottom: 20px;
}

.title {
  font-size: 19.2px;
  font-family: "Merriweather";
  font-weight: 900;
  color: #e17334;
}

.text {
  font-size: 12.8px;
  font-family: "Open Sans";
  color: #114e4e;
  text-align: center;
  margin-bottom: 20px;
}

.icon-container {
  display: flex;
  flex-direction: row;
  border: solid 2px;
  border-color: rgba(225, 115, 52, 0.21);
  border-radius: 4px;
  width: 268px;
  justify-content: space-around;
  margin-bottom: 30px;
}

.icon-container .design-icon {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #e17334;
}

.icon-container .fill-icon {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #e17334;
}

.icon-container .share-icon {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #e17334;
}

.icon1,.icon2,.icon3 {
  font-size: 12.8px;
  font-family: "Open Sans";
  color: #54585a;
  margin-top: 0;
}

.button {
  font-size: 16px;
  font-family: "Open Sans";
  color: white;
  background: #438792;
  text-transform: uppercase;
  border-radius: 21px;
  width: 129px;
  height: 42px;
  margin-bottom: 30px;
  border-style: solid;
}

.header-image-container {
  width: 100%;
  height: auto;
  margin: auto;
  background: white;
  display: flex;
  justify-content: center;
}

.header-awesome-image {
  width: 201px;
  height: 50px;
  margin-top: 30px;
  margin-bottom: 30px;
}

@media all and (min-width: 768px) {
  .page_main {
    height: 963px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .wrapper_main {
    padding-top: 0;
  }

  .content-container {
    margin-top: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .awesome-image {
    width: 419px;
    height: 105px;
  }

  .title {
    margin-bottom: 0;
    font-size: 24px;
  }

  .text {
    margin-top: 0;
    font-size: 16px;
    width: 330px;
  }

  .icon-container {
    width: 325px;
    height: 70px;
  }

  .icon1,.icon2,.icon3 {
    font-size: 16px;
    text-align: center;
  }

  .button-container {
    position: relative;
    top: 150px;
  }

  .header-image-container {
    justify-content: flex-start;
    margin-left: 40px;
  }
}
